/* Carousel */

.carousel {
    margin-bottom: 20px;

    img {
        padding: 3px;
        @include add-border(1px, #cccccc, all);
    }
}

/*  Tabs */

.tab-pane {
    padding: 20px 10px;
    @include border-radius(5px);
    margin: 5px;
}

.tab-primary .tab-pane, .tab-primary .nav-tabs > li.active > a, .tab-primary .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
    @include add-background($primary-color);
  
}

.tab-secondary .tab-pane, .tab-secondary .nav-tabs > li.active > a, .tab-secondary .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
    @include add-background($secondary-color);
  
}

.tab-light .tab-pane, .tab-light .nav-tabs > li.active > a, .tab-light .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
    @include add-background($light-color);
}

.tab-dark .tab-pane, .tab-dark .nav-tabs > li.active > a, .tab-dark .nav-tabs > li.active > a:hover, .nav-tabs > li.active > a:focus {
    @include add-background($dark-color);
  
}